@font-face {
  font-family: "poppinsRegular";
  src: url("./fonts/Poppins-Regular.ttf");
}
@font-face {
  font-family: "poppinsBold";
  src: url("./fonts/Poppins-Bold.ttf");
}
@font-face {
  font-family: "poppinsLight";
  src: url("./fonts/Poppins-Light.ttf");
}

*{
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}

/* Header */
header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: .8rem 3rem;
}

#logo {
  margin-right: auto;
  width:20rem;
}

header li {
  list-style: none;
  display: inline-block;
  padding: 0 20px;
}

header a {
  text-decoration: none;
  color: #000000;
  transition: all 0.3s ease 0s;
  font-size: 1.8rem;
  font-family: "poppinsRegular";
}

header a:hover {
  color: #FC8769;
}



@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,500,300,700);





.footer-distributed{
	background: #EBEBEB;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
	box-sizing: border-box;
	width: 100%;
	text-align: left;
	font: bold 16px sans-serif;
	padding: 55px 50px;
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right{
	display: inline-block;
	vertical-align: top;
}

/* Footer left */

.footer-distributed .footer-left{
	width: 40%;
}

.footer-distributed h3 span{
	color:  lightseagreen;
}

/* Footer links */

.footer-distributed .footer-links{
	color:  black;
	margin: 20px 0 12px;
	padding: 0;
}

.footer-distributed .footer-links a{
	display:inline-block;
	line-height: 1.8;
  font-weight:400;
	text-decoration: none;
	color:  inherit;
}

.footer-distributed .footer-company-name{
	color:  black;
	font-size: 14px;
	font-weight: normal;
	margin: 0;
}

/* Footer Center */

.footer-distributed .footer-center{
	width: 35%;
}



.footer-distributed .footer-center i.fa-envelope{
	font-size: 17px;
	line-height: 38px;
}

.footer-distributed .footer-center p{
	display: inline-block;
	color: black;
  font-weight:400;
	vertical-align: middle;
	margin:0;
}

.footer-distributed .footer-center p span{
	display:block;
	font-weight: normal;
	font-size:14px;
	line-height:2;
}

.footer-distributed .footer-center p a{
	color:  rgb(28, 82, 82);
	text-decoration: none;;
}

.footer-distributed .footer-links a:before {
  content: "|";
  font-weight:300;
  font-size: 20px;
  left: 0;
  color: black;
  display: inline-block;
  padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
  content: none;
}

/* Footer Right */

.footer-distributed .footer-right{
	width: 20%;
}

.footer-distributed .footer-company-about{
	line-height: 20px;
	color:  black;
	font-size: 13px;
	font-weight: normal;
	margin: 0;
}

.footer-distributed .footer-company-about span{
	display: block;
	color:  black;
	font-size: 14px;
	font-weight: bold;
	margin-bottom: 20px;
}

.footer-distributed .footer-icons{
	margin-top: 25px;
}

.footer-distributed .footer-icons a{
	display: inline-block;
	width: 35px;
	height: 35px;
	cursor: pointer;
	background-color:  black;
	border-radius: 2px;

	font-size: 20px;
	color: black;
	text-align: center;
	line-height: 35px;

	margin-right: 3px;
	margin-bottom: 5px;
}

/* If you don't want the footer to be responsive, remove these media queries */

@media (max-width: 880px) {

	.footer-distributed{
		font: bold 14px sans-serif;
	}

	.footer-distributed .footer-left,
	.footer-distributed .footer-center,
	.footer-distributed .footer-right{
		display: block;
		width: 100%;
		margin-bottom: 40px;
		text-align: center;
	}

	.footer-distributed .footer-center i{
		margin-left: 0;
	}

}
.home{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
}
.home-background{
  position: absolute;
  width: 100%;
  height: 100%;
  min-width: 60rem;
  z-index: -1;

}
.text-zone{
  width: 60%;
  height: 100%;
  padding: 0 5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.home-image-zone{
  width: 50%;
  height: 50%;
  display: flex;
  margin: 0 5rem;
  justify-content: center;
}
.home-image{
  max-width: 100%;
  height: auto;
}
.vet-title{
  font-family: "poppinsLight";
  font-size: 1.3rem;
  color: #726724;
}
.vet-h{
  font-family: "poppinsBold";
  font-size: 4rem;
}
.vet-p{
  font-family: "poppinsRegular";
  font-size: 1.5rem;
  text-align: justify;

}
.contactButton {
  background: #BBA833;
  color: white;
  font-family: inherit;
  padding: 0.45em;
  padding-left: 1.0em;
  font-size: 17px;
  font-weight: 500;
  border-radius: 0.9em;
  border: none;
  letter-spacing: 0.05em;
  display: flex;
  align-items: center;
  box-shadow: inset 0 0 1.6em -0.6em #b9a520;
  overflow: hidden;
  position: relative;
  height: 2.8em;
  padding-right: 3em;
  width: 11rem;
  font-family: "poppinsLight";

}

.iconButton {
  margin-left: 1em;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.2em;
  width: 2.2em;
  border-radius: 0.7em;
  box-shadow: 0.1em 0.1em 0.6em 0.2em #bda71b;
  right: 0.3em;
  transition: all 0.3s;
}

.contactButton:hover {
  transform: translate(-0.05em, -0.05em);
  box-shadow: 0.15em 0.15em #817211;
}

.contactButton:active {
  transform: translate(0.05em, 0.05em);
  box-shadow: 0.05em 0.05em #82720c;
}
.perks{
  display: flex;
  gap: 2rem;
}
.perk1{
  border-right: 5px solid #BBA833;
  font-family: "poppinsBold";
  padding-right: 2rem;
}
.perk2{
  border-right: 5px solid #BBA833;
  font-family: "poppinsBold";
  padding-right: 2rem;

}
.perk3{
  font-family: "poppinsBold";

}
.perk-p{
  font-family: "poppinsLight";
  font-size: 1.2rem;
}

