/* services section */

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  margin: 20px;
}

.card {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card-image {
  height: 300px;
  background-size: cover;
  background-position: center center;
}

.card-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
  width: 80%;
}

.card-text h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.card-text p {
  font-size: 1.2rem;
  opacity: 0;
  transition: all .3s ease-in;
}

.card-text p:hover {
  font-size: 1.2rem;
  opacity: 1;
}
.services-banner{
  display: flex;
  justify-content: center
}
.services-banner img{
  width: 95%;
  height: auto;
  border-radius: 12px;
}


  
